import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../../admin.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";

export interface LocationTypeConfig extends BaseConfig {
}

export let defaultLocationTypeConfig: LocationTypeConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: false,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: AdminEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT,
  delEndpoint: AdminEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/location-types",
  getChildren(id: number) {
    return "/admin/location-types?parent_id=" + id
  },
  create: "/admin/location-types/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/location-types/" + id + '/update'
  }
}
