import {Injectable} from '@angular/core';
import {BaseService} from "../../../../../base/services/base.service";
import {AuthService} from "../../../../common/modules/auth/services/auth.service";
import {NotifierService} from "../../../../../services/notifier/notifier.service";
import {HttpClient, HttpErrorResponse} from "@angular/common/http";
import {EventBusService} from "../../../../../services/eventbus/event-bus.service";
import {IParams} from "../../../../../base/endpoints/iparams";
import {AdminEndpoint} from "../../../admin.endpoint";
import {IPage} from "../../../../../base/models/IPage";
import {ICrudExample, ICrudExampleReq} from "../models/crudexample.model";
import {IAuth} from "../../../../common/modules/auth/models/auth.model";

@Injectable({
  providedIn: 'root'
})
export class CrudExampleService extends BaseService {

  constructor(
    authService: AuthService,
    notifier: NotifierService,
    private http: HttpClient,
    private eventBusService: EventBusService
  ) {
    super(authService, notifier)
  }

  search(key: string, endpoint: string, params: IParams) {
    let url = AdminEndpoint.CRUDEXAMPLES.SEARCH(endpoint, params)
    this.http.get<IPage<ICrudExample>>(url)
      .subscribe((crudExample: IPage<ICrudExample>) => {
        this.eventBusService.publish(key, crudExample)
      }, error => {
         this.handleError(error, (auth: IAuth) => {
          this.search(key, endpoint, params)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  fetchSingle(key: string, id: number) {
    this.http.get<ICrudExample>(AdminEndpoint.CRUDEXAMPLES.GET(id))
      .subscribe((crudExample: ICrudExample) => {
        this.eventBusService.publish(key, crudExample)
      }, (error: HttpErrorResponse) => {
         this.handleError(error, (auth: IAuth) => {
          this.fetchSingle(key, id)
        }, (err: HttpErrorResponse) => {
          this.eventBusService.publish(this.getErrorKey(key), err)
        })
      })
  }

  upsert(key: string, crudExampleReq: ICrudExampleReq, exId: number | null) {
    let endpoint = exId == null ? AdminEndpoint.CRUDEXAMPLES.CREATE() : AdminEndpoint.CRUDEXAMPLES.UPDATE(exId)
    let observable = exId == null ? this.http.post<number>(endpoint, crudExampleReq)
      : this.http.patch(endpoint, crudExampleReq)
    observable.subscribe(res => {
      this.eventBusService.publish(key, res)
    }, (err: HttpErrorResponse) => {
      this.handleError(err, (auth: IAuth) => {
        this.upsert(key, crudExampleReq, exId)
      }, (err: HttpErrorResponse) => {
        this.eventBusService.publish(this.getErrorKey(key), err)
      })
    })
  }

}
