import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../../admin.endpoint";


export interface AddressConfig extends BaseConfig {
  locationEndpoint: string,
  locationTypeEndpoint: string
}

export let defaultAddressConfig: AddressConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: AdminEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  delEndpoint: AdminEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  locationEndpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT,
  locationTypeEndpoint: AdminEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}

export let selfEndpoints = {
  endpoint: AdminEndpoint.ADDRESSS.ADDRESS_ENDPOINT,
  adminEndpoint: AdminEndpoint.ADDRESSS.ADDRESS_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/admin/addresses",
  create: "/admin/addresses/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/addresses/" + id + '/update'
  }
}

