import {AdminEndpoint} from "../../../admin.endpoint";
import {BaseConfig} from "../../../../../base/models/baseConfig";
import {Layout} from "../../../components/breadcrumb/breadcrumb.model";

export interface CrudExampleConfig extends BaseConfig {
}

export let defaultCrudExampleConfig: CrudExampleConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: AdminEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT,
  delEndpoint: AdminEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT
}

export let selfEndpoints = {
  endpoint: AdminEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT,
  adminEndpoint: AdminEndpoint.CRUDEXAMPLES.CRUDEXAMPLE_ENDPOINT_ADMIN,
}

export let selfRoutes = {
  list: "/admin/crudexamples",
  create: "/admin/crudexamples/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/crudexamples/" + id + '/update'
  }
}


