import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../../admin.endpoint";
import {BaseConfig} from "../../../../../../base/models/baseConfig";


export interface POptionConfig extends BaseConfig {
}

export let defaultPOptionConfig: POptionConfig = {
  defaultLayout: Layout.GRID,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: true,
  searchEndpoint: AdminEndpoint.POPTIONS.POPTION_ENDPOINT,
  delEndpoint: AdminEndpoint.POPTIONS.POPTION_ENDPOINT
}


export let selfRoutes = {
  list: "/admin/v2/poll-options",
  create: "/admin/v2/poll-options/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/v2/poll-options/" + id + '/update'
  }
}
