import {BaseConfig} from "../../../../../base/models/baseConfig";
import {Layout} from "../../../components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../admin.endpoint";
import {WebAppEndpoint} from "../../../../webapp/web-app.endpoint";

export interface LabelConfig extends BaseConfig {
  searchEndpointAdmin: string
}

export let defaultLabelConfig: LabelConfig = {
  defaultLayout: Layout.COLUMN,
  upsertOnDialog: true,
  showUserFilter: false,
  showDeleteButton: false,
  searchEndpointAdmin: AdminEndpoint.LABELS.LABEL_ENDPOINT_ADMIN,
  searchEndpoint: AdminEndpoint.LABELS.LABEL_ENDPOINT,
  delEndpoint: AdminEndpoint.LABELS.LABEL_ENDPOINT
}

export let selfRoutes = {
  list: "/admin/labels",
  getChildren(id: number) {
    return "/admin/labels?parent_id=" + id
  },
  create: "/admin/labels/create",
  update(id: number | null) {
    return id == null ? this.create : "/admin/labels/" + id + '/update'
  }
}
