import {BaseConfig} from "../../../../../../base/models/baseConfig";
import {Layout} from "../../../../components/breadcrumb/breadcrumb.model";
import {AdminEndpoint} from "../../../../admin.endpoint";


export interface LocationConfig extends BaseConfig {
	getByTypeEndpoint: string
}

export let defaultLocationConfig: LocationConfig = {
	defaultLayout: Layout.COLUMN,
	upsertOnDialog: false,
	showUserFilter: false,
	showDeleteButton: true,
	searchEndpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	delEndpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	getByTypeEndpoint: AdminEndpoint.LOCATIONTYPES.LOCATIONTYPE_ENDPOINT
}

export let selfEndpoints = {
	endpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT,
	adminEndpoint: AdminEndpoint.LOCATIONS.LOCATION_ENDPOINT_ADMIN,
}

export let selfRoutes = {
	list: "/admin/locations",
	getChildren(id: number) {
		return "/admin/locations?parent_id=" + id
	},
	create: "/admin/locations/create",
	update(id: number | null) {
		return id == null ? this.create : "/admin/locations/" + id + '/update'
	}
}
